 .floating-container {
     display: flex;
     width: 95%;
     height: 90%;
     box-shadow: 0 0 30px rgba(159, 157, 157, 0.3);
     position: relative;
     border-radius: 5px;
     padding: 5px;
     overflow: auto;
     text-align: center;
     background-color: var(--background-color-light);
     z-index: 1;

     .content {
         overflow: auto;
         margin: 0 10x;
         padding: 10px;
         border-radius: 8px;
         scroll-behavior: smooth;
         width: 100%;

         &::-webkit-scrollbar {
             width: 8px;
         }

         &::-webkit-scrollbar-track {
             background: rgba(159, 157, 157, 0.3);
             border-radius: 10px;
         }

         &::-webkit-scrollbar-thumb {
             background: var(--main-color);
             border-radius: 10px;
         }

         .about_img_div {
             display: flex;
             align-items: center;
             justify-content: space-around;
             margin: 30px auto;

             .about_pic {
                 max-width: 25%;
                 border-radius: 40%;
                 height: auto;
                 margin: auto 2.5%;
             }
         }

         .text_div {
             align-items: left;
             text-align: left;
             width: 90%;
             flex-direction: column;
             margin: 0 10px;
         }
     }
 }


 .span_title {
     color: var(--main-color);
     background: linear-gradient(135deg, rgba(201, 202, 200, 0.3) 0%, rgba(120, 204, 109, .01) 100%);
     padding: 4px 2px 4px 20px;
     border-radius: 50%;
 }

 .component_title {
     display: flex;
     justify-content: left;
     position: relative;
     padding-bottom: 0.5px;
     margin-bottom: 30px;

     &::after {
         content: '';
         position: absolute;
         bottom: 0;
         left: 0;
         width: 100%;
         height: 1px;
         background: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
     }
 }

 .CSM_pic {
     float: right;
     margin: 0 10px;
     border-radius: 5px;
     border: 2px solid var(--main-color);
     max-width: 400px;
     height: auto
 }

 .remove_p-margin {
     margin: 0;
 }

 @media all and (max-width: 1050px) {
     .floating-container {
         overflow: visible;
         height: auto;
         margin: 2% 0;
         box-shadow: 0 0 10px rgba(159, 157, 157, 0.3);

     }

     .component_title {
         justify-content: center;
     }

     .floating-container .content {
         overflow: visible;
         margin-top: 9vh;
     }
 }

 @media all and (max-width: 950px) {
     .background_div {
         flex-direction: column;
     }
 }

 @media all and (max-width: 750px) {
     .floating-container .content {
         overflow: visible;
         margin-top: 7vh;
     }

     .about_img_div {
         flex-direction: column;

         .about_pic {
             width: 20vw;
         }
     }

     .floating-container .about_pic {
         max-width: 80%;
     }
 }

 @media all and (max-width: 750px) {
     .CSM_pic {
         max-width: 300px;
     }
 }